export default {
	name: '',
	data () {
		return {
			payMethod: [
				{icon: '/static/images/y3@2x.png', text: '微信'},
				{icon: '/static/images/y4@2x.png', text: '支付宝'},
			],
			current: 0
		}
	},
	onLoad () {

	},
	methods: {
		onPayMethodClick (item, index) {
			if(this.current != index) {
				this.current = index;
			}
			this.$emit('onPayMethodClick', (index + 1))
		},
	}
}
